import React from "react";
import Chart from "react-apexcharts";
const Diagram = ({ height, color, tooltip, rates }) => {
  const state = {
    options: {
      chart: {
        type: 'area',
        stacked: false,
        sparkline: {
          enabled: true,
        },
       
        toolbar: {
          autoSelected: 'zoom'
        },
        background: "transparent",
      },
      theme: {
        mode: "dark",
        monochrome: {
          enabled: false,
          color: "#fff",
          shadeTo: "dark",
          shadeIntensity: 0.65,
        },
      },
      markers: {
        size: 0,
        style: 'hollow',
      },
      stroke: {
        curve: 'straight'
      },
      tooltip: {
        enabled: tooltip
      },
      fill: {
        type: 'gradient',
        gradient: {
          shadeIntensity: 1,
          opacityFrom: 0.7,
          opacityTo: 0.9,
          stops: [0, 100]
        }
      },
      
      colors: [color],
      xaxis: {
        categories: rates && rates.map(item => item.date),
      },
    },
    series: [
      {
        name: "price",
        data: rates && rates.map(item => item.price),
      },
    ],

  }
  return (
    <>
 <Chart
        options={state.options}
        series={state.series}
        type='area'
        height={height}
      /> 

    </>
  );
};

export default Diagram;
